<template>
  <div  class="col s12 m6 l4">
    <Loader v-if="! this.base" />
    <div v-else class="card light-blue bill-card">
      <div class="card-content white-text">
        <span class="card-title">Счет в валюте</span>

        <p class="currency-line"
          v-for="cur in currencies" :key="cur"
        >
          <span>{{currencyFilter(getCurrency(cur), cur)}}</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import Loader from '@/components/app/Loader.vue'

export default {
  props: ["rate"],
  components:{
    Loader,
  },

  data(){
    return{
      loader: true,
      currencies:['USD', 'RUB', 'UZS',]
    }
  },

  computed: {
    base() {
      return (
        this.$store.getters.info.bill / (this.rate["RUB"] / this.rate["USD"])
      );
    },
  },

  methods:{
    getCurrency(currency){
      return Math.floor(this.base * this.rate[currency])
    },

    currencyFilter(value, currency = 'EUR'){
      return new Intl.NumberFormat('ru-RU', {
        style: 'currency',
        currency,
      }).format(value)
    }
  },
};
</script>